import Master from '../components/Master';

import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';

import fetch from 'isomorphic-unfetch';

const Index = (props) => (
	<Master>
		<div className="row">
			<div className="col-12">
				<h2>Precio del Bitcoin</h2>
				<Precio
					precio={props.precioBitcoin}
				/>
			</div>
		</div>
		<div className="row">
			<div className="col-md-8">
				<h2 className="mb-3">Noticias sobre Bitcoin</h2>
				<Noticias
					noticias={props.noticias}
				/>
			</div>
			<div className="col-md-4">
				<h2 className="mb-3">Proximos eventos Bitcoin</h2>
				<Eventos
					eventos={props.eventos}
				/>
			</div>
		</div>
	</Master>
)

Index.getInitialProps = async () => {
	const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
	const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=f105df52fd134186b00aa9a89d53db2b&language=es');
	const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Argentina&token=LPM4RDM67UE6JK5LX5BT&locale=es_ES')

	const resPrecio = await precio.json();
	const resNoticias = await noticias.json();
	const resEventos = await eventos.json();

	return {
		precioBitcoin: resPrecio.data.quotes.USD,
		noticias: resNoticias.articles,
		eventos: resEventos.events
	}
}

export default Index;